let configCache = function () {
    let _cache = new Map(); //
    return {
        getCache(k) {
            return _cache[k];
        },
        setCache(k, v) {
            _cache[k] = v;
            return this;//,:configCache.removeCache(k).setCache(k, v)
        },
        removeCache(k) {
            delete _cache[k];
            return this;
        }
    }
}();//

export {
    configCache
};